html,body{margin:0;padding:0;border:0;width:100%;height:100%;background-color:#c9c9c9}.main{display:flex;justify-content:center;flex-direction:column;margin:0 auto}.top-menu{margin-top:200px;display:flex;justify-content:center;align-items:center}.top-menu h1{font-family:'Poiret One', cursive;font-size:50px;letter-spacing:7px}#audio-container{margin-top:50px;padding-bottom:50px;display:flex;justify-content:center;align-items:center}.audio-wrapper{display:flex;box-shadow:0 0 10px #727272, 0 0 40px #727272, 0 0 80px #727272}#audio-note{border:1px solid gray}.note-container{padding-top:15px;width:1060px;height:735px;padding-left:25px;border:1px solid gray;background-color:#e6e6e6}.note-container div{display:flex;height:4%}.checkbox-label{width:60%;height:60%}.checkbox-label>input{position:absolute;width:25px;margin-left:10px;opacity:0;cursor:pointer}#checkbox-style{width:45px;height:15px;border:1px solid #111;border-radius:3px;background-color:gray;opacity:1}.checkbox-label input:checked ~ #checkbox-style{background-color:#9dff4e;transition:all 0.3s ease 0s;box-shadow:0 0 10px #9dff4e, 0 0 40px #9dff4e, 0 0 80px #9dff4e}.bottom-menu{position:relative;display:flex;justify-content:space-evenly}.bottom-menu-left{display:flex;justify-content:space-between;width:200px}.bottom-menu-right{display:flex;justify-content:space-between;font-size:35px}.bottom-menu-right div a{text-decoration:none;color:black;font-family:'Poiret One', cursive;letter-spacing:4px}.bottom-menu-right div{margin-right:40px}.bottom-menu-left button{background-color:transparent;border:none}.bottom-menu-left button i{font-size:35px}.tempo-input{font-family:'Poiret One', cursive;font-size:30px;letter-spacing:4px;display:flex;align-items:center}.tempo-input input{height:40px;width:80px;margin-left:10px;border-radius:5px;font-size:30px;text-align:center;font-family:'Poiret One', cursive;font-weight:600;letter-spacing:4px}.github-link,.linkedin-link{position:relative;display:inline-block}.github-link>a:after,.linkedin-link>a:after{content:'';position:absolute;left:0;display:inline-block;height:1em;width:100%;border-bottom:1px solid;margin-top:10px;opacity:0;transition:opacity 0.35s, transform 0.35s;transform:scale(0, 1)}.github-link>a:hover:after,.linkedin-link>a:hover:after{opacity:1;transform:scale(1)}
